// This file is shared across the demos.
import Link from 'next/link';
import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

export default class FerryMenu extends React.Component {
  static async getInitialProps() {
    const URL = 'https://ferrytrackerserver.now.sh/graphql';
    const query = `{
      allRoutes {
        id
        routeName
      }
    }`;
    const res = await request(URL, query);
    console.log(`All routes fetched. Count: ${res.allRoutes.length}`);
    return res;
  };

  render() {
    return  (
      <List>
        {console.log(this.props)}
        {this.props.allRoutes.map(route => (
        <Link
          prefetch
          as={`post?id=${route.routeName}`}
          href={{ pathname: '/post', query: { id: route.routeName } }}
        >
         <ListItem button key={route.id}>
          <ListItemText primary={route.routeName} />
          </ListItem>
        </Link>
    ))}
      </List>
    );
  }
} 
